.cpanel{
	position:relative;
	height:1300px;
	background-color:#f8f9fa;
}

.leftNavi{
	background-color:grey;
	float:left;
	display:inline-block;
	height:100%;
	width:100px;
}

.cpanel_btn{
	position:relative
}


.cpanel_btn img{
	width:62px;
	margin-top:50px;
	
}

.cpanel_btn img:hover{
	position:relative;
	width:68px;
	margin-top:44px;
	cursor:pointer;
	
	

}

.panel{
	position:absolute;
	color:black;
	display:inline-block;
	text-align:left;
	float:left;
	width:100%;
	left:0px;
	
}


.products_manager{
	font-size:26px;
	font-family:"Muli";
	font-weight:600;
	position:relative;
	width:100%

	
}

.title_panel{
	border-bottom:1px solid black;
	font-size:26px;
	max-width:100%;
	height:50px;
	padding-top:20px;
	padding-left:20px;
	background-color:#DDDDDD;
	margin-left:100px;

	
}
